.musicNotes { 
    background: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.2)),to(rgba(0, 0, 0, 0.2))),url("../../components/images/half-music.jpg") no-repeat center center fixed; 
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("../../components/images/half-music.jpg") no-repeat center center fixed; 
    background: -o-linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("../../components/images/half-music.jpg") no-repeat center center fixed; 
    background: linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("../../components/images/half-music.jpg") no-repeat center center fixed;
    background-size: cover;
    min-height: 580px; 
    height:auto !important; 
    height: 580px; 
    color: white;
    font-family: 'Times New Roman', Times, serif;
  }

  .musicNotes h6 {
      font-weight: bold;
  }

  .musicNotes h5 {
      border: 1px solid white;
      padding: 3%;
      margin: 3%;
      text-align: left;
  }

  .musicNotes h3 {
      text-decoration: underline;
  }


