.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.col-md-6 {
  padding-top: 2%;
}

.list-group-item {
  background: none;
  color: white;
}

.top {
  background-image: url("./components/images/brick2.jpg");
  background-size: cover;
  background-position: center center;
  background-repeat: no-repeat;
  padding-top: 15px;
  -webkit-box-shadow: 0px 20px 50px 5px black;
          box-shadow: 0px 20px 50px 5px black;
}

.nav-link, .Header {
  color: white;
}

.nav-link:hover{
  color: gray;
}

.nav-link:focus{
  color: white;
}

.Home {
  background: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.2)),to(rgba(0, 0, 0, 0.2))),url("./components/images/old-music.jpg") no-repeat center center fixed;
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("./components/images/old-music.jpg") no-repeat center center fixed;
  background: -o-linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("./components/images/old-music.jpg") no-repeat center center fixed;
  background: linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("./components/images/old-music.jpg") no-repeat center center fixed;
  background-size: cover;
  min-height: 580px; 
  height:auto !important; 
  height: 580px; 
  font-family: "Palatino Linotype", "Book Antiqua", Palatino, serif;
  font-weight: bolder;
  text-transform: uppercase;
  text-decoration: none;
  color: white;
  font-size: 52px;
  letter-spacing: 1px;
  padding-top: 150px;
  text-shadow: black 20px 0px 20px;
}

.SignupForm, .LoginForm {
  background: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.2)),to(rgba(0, 0, 0, 0.2))),url("./components/images/fleur.jpg") no-repeat center center fixed;
  background: -webkit-linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("./components/images/fleur.jpg") no-repeat center center fixed;
  background: -o-linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("./components/images/fleur.jpg") no-repeat center center fixed;
  background: linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("./components/images/fleur.jpg") no-repeat center center fixed;
  background-size: cover;
  min-height: 580px; 
  height:auto !important; 
  height: 580px; 
  font-family: "Palatino Linotype", "Book Antiqua", Palatino, serif;
  font-weight: 500;
  text-transform: uppercase;
  text-decoration: none;
  color: white;
  letter-spacing: 1px;
  padding-top: 20px;
}

.LoginForm H1 {
  margin-bottom: 40px;
}

.SignupForm H1 {
  margin-bottom: 40px;
}

.Header p {
  font-size: 25px;
}


