.musicList { 
    background: -webkit-gradient(linear,left top, left bottom,from(rgba(0, 0, 0, 0.2)),to(rgba(0, 0, 0, 0.2))),url("../../components/images/dark-stands.jpg") no-repeat center center fixed; 
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("../../components/images/dark-stands.jpg") no-repeat center center fixed; 
    background: -o-linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("../../components/images/dark-stands.jpg") no-repeat center center fixed; 
    background: linear-gradient(rgba(0, 0, 0, 0.2),rgba(0, 0, 0, 0.2)),url("../../components/images/dark-stands.jpg") no-repeat center center fixed;
    background-size: cover;
    min-height: 580px; 
    height:auto !important; 
    height: 580px; 
    font-family: "Palatino Linotype", "Book Antiqua", Palatino, serif;
    /* height: auto !important;
    position: relative;
    margin: 0 auto;
    height: 100%; */
  }

  .form-control, .form-group {
    background: none;
    color: white;
    border: 1px dashed white;
  }

  .form-control ::-moz-selection {
    color: black;
  }

  .form-control ::selection {
    color: black;
  }

  .form-group ::-webkit-input-placeholder {
    color: white;
  }

  .form-group :-ms-input-placeholder {
    color: white;
  }

  .form-group ::placeholder {
    color: white;
  }



